
import photo from '../../../static/userphoto.jpeg';
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'page',
components: {
XHeader
},
data() {
return {
title: "项目管理",
type: '1',
uid: 0,

prolist: [{

}]
}
},
created() {

this.uid = this.util.getUid();
if(this.uid == null || this.uid == undefined) {
this.uid = 3;
}
this.initPro();

},
methods: { //初始化事件
initPro: function() {

let params = {
uid: this.uid

}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.GETMYPROJECT, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.prolist = data;
} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},openProjectFlow:function(index){
console.log(index);
var data=this.prolist[index];
this.$router.push({ name: 'ProjectDesc', params:{ proid: data.id }});
}
}
}



